import React, { useState } from 'react'

import './dashboard.css'
import Sidebar from './components/SideBar/SideBar'
import StatsCards from './components/StatsCard/StatsCard'
import OrderList from './features/orders/orderList/OrderList'
import OrderDetail from './features/orders/orderDetails/OrderDetail'
import UnderDevelopment from './features/underdevelopment/UnderDevelopment'
import Header from './components/Header/Header'

function Dashboard() {
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false)
  const [currentPage, setCurrentPage] = useState('Order List')
  const [sidebarOpen, setSidebarOpen] = useState(false)

  const handlePageChange = (pageName) => {
    setCurrentPage(pageName)
    // Close sidebar on mobile after selection
    if (window.innerWidth <= 768) {
      setSidebarOpen(false)
    }
  }

  const toggleSidebar = () => {
    if (window.innerWidth <= 768) {
      setSidebarOpen(!sidebarOpen)
    } else {
      setSidebarCollapsed(!sidebarCollapsed)
    }
  }

  const renderCurrentPage = () => {
    switch (currentPage) {
      case 'Order List':
        return (
          <>
            <StatsCards />
            <OrderList />
          </>
        )
      case 'Order Detail':
        return <OrderDetail />
      default:
        return <UnderDevelopment pageName={currentPage} />
    }
  }

  return (
    <div className="admin-dashboard-container">
      <Sidebar 
        collapsed={sidebarCollapsed} 
        open={sidebarOpen}
        onToggle={toggleSidebar}
        onPageChange={handlePageChange}
        currentPage={currentPage}
      />
      <div className={`admin-main-content ${sidebarCollapsed ? 'sidebar-collapsed' : 'sidebar-expanded'}`}>
        <Header onToggleSidebar={toggleSidebar} />
        <div className="admin-content-wrapper">
          {renderCurrentPage()}
        </div>
      </div>
      {sidebarOpen && <div className="admin-sidebar-overlay" onClick={() => setSidebarOpen(false)}></div>}
    </div>
  )
}

export default Dashboard