import React,  { useState } from 'react'
import './Sidebar.css'

const Sidebar = ({ collapsed, open, onToggle, onPageChange, currentPage }) => {
  const [expandedItems, setExpandedItems] = useState({ 'Order': true })

  const menuItems = [
    { name: 'Home', icon: '🏠', path: '/home' },
    { name: 'Products', icon: '📦', path: '/products' },
    { name: 'Cart', icon: '🛒', path: '/cart' },
    { 
      name: 'Order', 
      icon: '📋', 
      path: '/order',
      submenu: [
        { name: 'Order List', path: '/order/list' },
        { name: 'Order Detail', path: '/order/detail' }
      ]
    },
    { name: 'Chat', icon: '💬', path: '/chat' },
    { name: 'Customer', icon: '👥', path: '/customer' },
    { name: 'Sellers', icon: '🏪', path: '/sellers' },
    { name: 'Shop', icon: '🛍️', path: '/shop' },
    { name: 'Products Alt', icon: '📋', path: '/products-alt' },
    { name: 'Wallet', icon: '💰', path: '/wallet' },
    { name: 'Setting', icon: '⚙️', path: '/setting' },
    { name: 'Auth', icon: '🔐', path: '/auth' },
    { name: 'Widgets', icon: '🧩', path: '/widgets' },
    { name: 'Form', icon: '📝', path: '/form' }
  ]

  const toggleSubmenu = (itemName) => {
    setExpandedItems(prev => ({
      ...prev,
      [itemName]: !prev[itemName]
    }))
  }

  const handleItemClick = (item) => {
    if (item.submenu) {
      toggleSubmenu(item.name)
    } else {
      onPageChange(item.name)
    }
  }

  const handleSubmenuClick = (subItem) => {
    onPageChange(subItem.name)
  }

  return (
    <>
      <aside className={`admin-sidebar ${collapsed ? 'collapsed' : ''} ${open ? 'mobile-open' : ''}`}>
        <div className="admin-sidebar-header">
          <div className="admin-logo">
            <div className="admin-logo-icon">M</div>
            {!collapsed && <span className="admin-logo-text">MagicBox</span>}
          </div>
          <button className="admin-sidebar-toggle" onClick={onToggle}>
            {collapsed ? '→' : '←'}
          </button>
        </div>

        {!collapsed && (
          <div className="admin-sidebar-search">
            <input 
              type="text" 
              placeholder="Search" 
              className="admin-search-input"
            />
          </div>
        )}

        <div className="admin-sidebar-section">
          {!collapsed && <span className="admin-section-title">Client App</span>}
        </div>

        <nav className="admin-sidebar-nav">
          {menuItems.slice(0, 5).map((item) => (
            <div key={item.name} className="admin-nav-item-group">
              <div 
                className={`admin-nav-item ${currentPage === item.name || (item.submenu && item.submenu.some(sub => sub.name === currentPage)) ? 'active' : ''}`}
                onClick={() => handleItemClick(item)}
              >
                <span className="admin-nav-icon">{item.icon}</span>
                {!collapsed && (
                  <>
                    <span className="admin-nav-text">{item.name}</span>
                    {item.submenu && (
                      <span className={`admin-nav-arrow ${expandedItems[item.name] ? 'expanded' : ''}`}>
                        ▼
                      </span>
                    )}
                  </>
                )}
              </div>
              {item.submenu && !collapsed && (
                <div className={`admin-submenu ${expandedItems[item.name] ? 'expanded' : ''}`}>
                  <div className="admin-submenu-content">
                    {item.submenu.map((subItem) => (
                      <div 
                        key={subItem.name}
                        className={`admin-submenu-item ${currentPage === subItem.name ? 'active' : ''}`}
                        onClick={() => handleSubmenuClick(subItem)}
                      >
                        <span className="admin-submenu-text">{subItem.name}</span>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>
          ))}
        </nav>

        <div className="admin-sidebar-section">
          {!collapsed && <span className="admin-section-title">Admin Panel</span>}
        </div>

        <nav className="admin-sidebar-nav">
          {menuItems.slice(5).map((item) => (
            <div 
              key={item.name}
              className={`admin-nav-item ${currentPage === item.name ? 'active' : ''}`}
              onClick={() => handleItemClick(item)}
            >
              <span className="admin-nav-icon">{item.icon}</span>
              {!collapsed && <span className="admin-nav-text">{item.name}</span>}
            </div>
          ))}
        </nav>
      </aside>
    </>
  )
}

export default Sidebar